export default {
    state: {
        filter: [
            {
                id: 0,
                name: 'Новинки',
                tag: 'new',
                state: true
            },
            {
                id: 1,
                name: 'Есть в наличии',
                tag: 'inStock',
                state: false
            },
            {
                id: 2,
                name: 'Контрактные',
                tag: 'contract',
                state: false
            },
            {
                id: 3,
                name: 'Эксклюзивные',
                tag: 'exclusive',
                state: false
            },
            {
                id: 4,
                name: 'Распродажа',
                tag: 'sale',
                state: false
            },
        ],
        openFilter: false
    },
    mutations: {
        toggle(state, id) {
            state.filter[id].state = !state.filter[id].state
        },
        openFilter(state) {
            state.openFilter = true
        },
        closeFilter(state) {
            state.openFilter = false
        },
    },
    actions: {
    },
    getters: {
        getFilterState(state) {
            return state.openFilter
        },
        filter(state) {
            return state.filter
        },
        activeFilters(state) {
            let active = []
            for( let i = 0; i < state.filter.length; i++) {
                if (state.filter[i].state) {
                    active.push(state.filter[i].tag)
                }
            }
            return active
        }
    }
}
