<template>
  <div class="v-popup">
    <ul>
      <li v-on:click="closePopup" name="priceDown">Сначала Дорогие</li>
      <li v-on:click="closePopup" name="priceUp">Сначала Недорогие</li>
      <li v-on:click="closePopup" name="default">Сначала Популярные</li>
    </ul>
  </div>
</template>

<script>
import  {mapActions, mapMutations } from 'vuex'
export default {
  name: "popup",
  methods: {
    ...mapMutations([
      "setValue"
    ]),
    ...mapActions([
      "setFilter", "closeModal"
    ]),
    closePopup(e) {
      this.setValue(e.target.innerHTML)
      this.closeModal(e.target.getAttribute('name'))
      this.setFilter()
      this.$emit('closePopup')
    }
  }
}
</script>

<style lang="scss" scoped>
  .v-popup {
    position: absolute;
    right: 64px;
    width: 250px;
    z-index: 11;
    ul {
      list-style-type: none;
      margin: 0;
    }
    li {
      padding: 16px 0 16px 24px;
      cursor: pointer;
      background-color: #FFF;
      font-weight: 500;
      font-size: 12px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      &:hover {
        background-color: #7BB899;
      }
    }
    &__header, &__footer {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__content {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
</style>