<template>
  <div class="navbar">
    <button class="menu"></button>
    <router-link to="/" class="logo">Colors</router-link>
    <nav>
      <ul>
        <li v-for="link in getLinks">
          <router-link v-bind:to="link.link">{{link.name}}</router-link>
        </li>
      </ul>
    </nav>
    <div class="phone">+7 (495) 221-77-69</div>
    <div class="actions">
      <div class="activities"><img src="../assets/img/icons/search.svg" alt="search"></div>
      <div class="activities"><img src="../assets/img/icons/profile.svg" alt="profile"></div>
      <div class="activities"><img src="../assets/img/icons/heart.svg" alt="heart"></div>
      <div v-on:click="openCart" :class="{ empty: !getCount }"><span>{{ getCount }}</span></div>
    </div>
  </div>
  <Cart v-if="getCartState"></Cart>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import Cart from "./Cart";
export default {
  name: "Navbar",
  components: { Cart },
  computed: mapGetters(["getLinks", "getCartState", "getCount"]),
  methods: {
    ...mapMutations([
      "openCart"
    ]),
  }
}
</script>

<style lang="scss" scoped>
  @font-face {
    font-family: Hoves;
    src: url(../assets/fonts/TTHoves-Regular.ttf);
  }
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 36px 64px;
    position: relative;
    a {
      color: #1F2020;
      text-decoration: none;
      text-transform: uppercase;
    }
  }
  .menu {
    display: none;
    height: 21px;
    width: 36px;
    border: none;
    outline: none;
    background: url("../assets/img/icons/menu.svg") no-repeat 50% 50%;
  }
  .logo {
    font-family: 'Hoves', sans-serif;
    font-size: 30px;
    font-weight: 400;
    text-transform: uppercase;
    line-height: 26px;
    cursor: pointer;
    display: flex;
    &:active {
      position: relative;
      top: 1px;
    }
    &:after {
      content: '\2022';
      position: relative;
      top: -10px;
      left: -3px;
    }
  }
  nav {
    ul {
      list-style-type: none;
      display: flex;
      margin: 0;
      padding: 0;
    }
    li:not(:last-child) {
      margin-right: 25px;
    }
    a {
      font-size: 14px;
      letter-spacing: 0.06em;
    }
  }
  .phone {
    letter-spacing: -0.02em;
    font-size: 16px;
    &:after {
      content: "Заказать звонок";
      display: block;
      cursor: pointer;
      opacity: 0.3;
      font-size: 14px;
      letter-spacing: 0;
    }
  }
  .actions {
    display: flex;
    div {
      width: 22px;
      height: 22px;
      cursor: pointer;
      margin-right: 26px;
      &:not(:last-child) {
      }
      &:last-child {
        display: flex;
        justify-content: center;
        margin-right: 0;
        background-color: #7bb899;
        border-radius: 50%;
        text-align: center;
        font-weight: 500;
        font-size: 12px;
        letter-spacing: 0.06em;
        span {
          position: relative;
          top: 3px;
        }
      }
      &:active {
        position: relative;
        top: 1px;
      }
    }
    .empty {
      pointer-events: none;
      opacity: 0;
    }
  }
  @media (max-width: 1200px) {
    .phone {
      display: none;
    }
  }
  @media (max-width: 1000px) {
    nav {
      display: none;
    }
    .activities {
      display: none;
    }
    .menu {
      display: block;
      margin-left: -6px;
    }
  }
  @media (max-width: 750px) {
    .navbar {
      padding: 0;
    }
  }
</style>