<template>
  <popup
    v-if="isInfoPopupVisible"
    v-on:closePopup="closeInfoPopup"
  />
  <button
    v-on:click="showPopupInfo"
  >
    {{getValue}}
    <img src="../assets/img/icons/triangle.svg" alt="select-triangle">
  </button>
</template>

<script>
import popup from "./popup/popup";
import { mapGetters, mapMutations } from 'vuex'
export default {
  name: "SortBy",
  components: { popup },
  computed: mapGetters(["getValue"]),
  data() {
    return {
      isInfoPopupVisible: false
    }
  },
  methods: {
    ...mapMutations([
      "openModal"
    ]),
    closeInfoPopup() {
      this.isInfoPopupVisible = false
    },
    showPopupInfo() {
      this.openModal()
      this.isInfoPopupVisible = true
    }
  }
}
</script>

<style lang="scss" scoped>
  button {
    display: flex;
    align-items: center;
    font-family: 'Inter', sans-serif;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    background: inherit;
    border: none;
    outline: none;
    cursor: pointer;
  }
  img {
    margin-left: 3px;
  }
</style>