export default {
    state: {
        modalIsOpen: false,
        activeSort: 'default',
        sortValue: 'Сначала популярные'
    },
    mutations: {
        setValue(state, value) {
            state.sortValue = value
        },
        openModal(state) {
            state.modalIsOpen = true
        },
        closeModal(state, choice) {
            state.activeSort = choice
            state.modalIsOpen = false
        },
    },
    actions: {
    },
    getters: {
        getValue(state) {
            return state.sortValue
        },
        modalState(state) {
            return state.modalIsOpen
        },
        sortState(state) {
            return state.activeSort
        },
    }
}
