<template>
  <div class="filter" :class="{ active: getFilterState}" v-on:click="closeFilter">
    <div class="rectangle"></div>
    <label class="switch" v-for="item in filter">
      <span class="title">{{ item.name }}</span>
      <input type="checkbox" :checked="item.state" v-on:change="onToggle(item.id)">
      <span class="slider round"></span>
    </label>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from "vuex"
export default {
  name: "Filter",
  computed: mapGetters(["filter", "getFilterState"]),
  methods: {
    ...mapMutations(["toggle", "closeFilter"]),
    ...mapActions(["setFilter"]),
    onToggle(id) {
      this.toggle(id)
      this.setFilter()
    }
  },
  async mounted() {
    this.setFilter()
  }
}
</script>

<style scoped>
  .rectangle {
    display: none;
    height: 4px;
    width: 28px;
    background: #1F2020;
    opacity: 0.6;
    border-radius: 40px;
    align-self: center;
    margin-bottom: 38px;
  }
  .filter {
    display: flex;
    flex-direction: column;
  }
  .title {
    position: absolute;
    left: 48px;
    display: flex;
    width: 150px;
    cursor: pointer;
  }
  .switch {
    position: relative;
    display: inline-block;
    width: 36px;
    height: 22px;
    margin-bottom: 10px;
  }
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #F2F2F2;
    -webkit-transition: .4s;
    transition: .4s;
  }

  .slider:before {
    position: absolute;
    content: "";
    height: 8px;
    width: 8px;
    left: 7px;
    bottom: 7px;
    background-color: #1F2020;
    -webkit-transition: .4s;
    transition: .4s;
  }

  input:checked + .slider {
    background-color: #7BB899;
  }

  input:checked + .slider:before {
    -webkit-transform: translateX(12px);
    -ms-transform: translateX(12px);
    transform: translateX(12px);
  }

  .slider.round {
    border-radius: 34px;
  }

  .slider.round:before {
    border-radius: 50%;
  }

  @media (max-width: 1000px) {
    .filter {
      display: none;
    }
    .rectangle {
      display: block;
    }
    .active {
      display: flex;
      width: calc(100% - 48px);
      height: 100%;
      position: fixed;
      z-index: 11;
      background: white;
      padding: 12px 24px 20px;
      left: 0;
      border-top-left-radius: 24px;
      border-top-right-radius: 24px;
    }
  }
</style>