export default {
    state: {
        activeLink: 0,
        links: [
            {
                id: 0,
                color: 'green',
                path: 'colors.png'
            },
            {
                id: 1,
                color: 'blue',
                path: 'blue.jpg'
            },
            {
                id: 2,
                color: 'gray',
                path: 'gray.jpg'
            },
            {
                id: 3,
                color: 'pink',
                path: 'pink.jpg'
            },
            {
                id: 4,
                color: 'purple',
                path: 'purple.jpg'
            },
        ],
    },
    mutations: {
        nextImage(state) {
            if (state.activeLink + 1 !== state.links.length ) {
                state.activeLink += 1
            } else {
                state.activeLink = 0
            }
        },
        previewImage(state) {
            if (state.activeLink !== 0) {
                state.activeLink -= 1
            } else {
                state.activeLink = state.links.length - 1
            }
        },
    },
    actions: {
    },
    getters: {
        active(state) {
            return state.activeLink
        },
        images(state) {
            return state.links
        },
        activeImage(state) {
            return state.links[state.activeLink].path
        }
    }
}
