export default {
    state: {
        cart: [],
        count: 0,
        total: 0,
        isOpen: false
    },
    mutations: {
        openCart(state) {
            state.isOpen = true
        },
        closeCart(state) {
            state.isOpen = false
        },

        recount(state) {
            state.count = 0
            state.total = 0
            state.cart.forEach(e => state.count += e.item.quantity)
            state.cart.forEach(e => state.total += e.item.quantity * e.item.price)
        },
        addItem(state, item) {
            item.item.quantity = 1
            state.cart.push(item)
        },
        removeItem(state, item) {
            state.cart = state.cart.filter(product => product.item.id !== item.item.id)
        },
        increase(state, item) {
            item.item.quantity++
        },
        decrease(state, item) {
            item.item.quantity--
        },
        clearCart(state) {
            state.cart = []
        },

    },
    actions: {
        addItem(state, item) {
            let result = state.state.cart.filter(product => product.item.id === item.id)
            if(result.length === 0) {
                state.commit('addItem', { item })
            } else {
                state.commit('increase', { item: state.state.cart[state.state.cart.findIndex(product => product.item.id === item.id)].item })
            }
            state.commit('recount')
        },
        removeItem(state, item) {
            let result = state.state.cart.filter(product => product.item.id === item.id)
            if(result[0].item.quantity > 1) {
                state.commit('decrease', { item })
            } else {
                state.commit('removeItem', { item: state.state.cart[state.state.cart.findIndex(product => product.item.id === item.id)].item })
            }
            state.commit('recount')
        },
        remove(state, item) {
            state.commit('removeItem', { item: state.state.cart[state.state.cart.findIndex(product => product.item.id === item.id)].item })
            state.commit('recount')
        },
        clearCart(state) {
            state.commit('clearCart')
            state.commit('recount')
        },
    },
    getters: {
        getCartItems(state) {
            return state.cart
        },
        getCartState(state) {
            return state.isOpen
        },
        getCount(state) {
            return state.count
        },
        getTotalPrice(state) {
            return state.total
        },
    }
}
