<template>
  <div class="cart">
    <div class="head">
      <p>Корзина</p>
      <button v-on:click="closeCart">
        <img src="../assets/img/icons/x.svg" alt="close-button">
      </button>
    </div>
    <div class="clear">
      <p>{{getCount}} товаров</p>
      <button v-on:click="clearCart">очистить список</button>
    </div>
    <div v-if="getCount" class="wrapper">
      <div class="container">
        <div class="item" v-for="product in getCartItems">
          <div class="helper">
            <img class="preview" :src="require(`../assets/img/items/` + product.item.image)" alt="itemPreview">
            <div class="about">
              <p>{{ product.item.name }}</p>
              <span>{{ product.item.price * product.item.quantity }} &#8381;</span>
            </div>
          </div>
          <div class="helper">
            <div class="count">
              <button class="minus" v-on:click="removeItem(product.item)"></button>
              <span>{{ product.item.quantity }}</span>
              <button class="plus" v-on:click="addItem(product.item)"></button>
            </div>
            <button class="remove" v-on:click="remove(product.item)"><img src="../assets/img/icons/x.svg" alt="remove-button"></button>
          </div>
        </div>
      </div>

      <div class="footer">
        <div class="total">
          <span>Итого</span>
          <p>{{getTotalPrice.toLocaleString()}}&#8381;</p>
        </div>
        <button class="submit">Оформить заказ</button>
      </div>
    </div>
    <div v-else>
      <p class="message">Корзина пуста...</p>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapGetters, mapActions } from 'vuex'
export default {
  name: "Cart",
  computed: mapGetters(["getCount", "getCartItems", "getTotalPrice"]),
  methods: {
    ...mapMutations([
      "closeCart"
    ]),
    ...mapActions([
      "clearCart", "addItem", "remove", "removeItem"
    ]),
  }
}
</script>

<style lang="scss" scoped>
  .cart {
    position: fixed;
    z-index: 11;
    top: 0;
    right: 0;
    background: white;
    height: calc(100% - 72px);
    width: calc(600px - 80px);
    padding: 32px 40px 40px;
  }
  .head {
    margin-bottom: 80px;
    p {
      font-weight: 500;
      font-size: 30px;
      letter-spacing: -0.04em;
      margin: 0;
    }
    button {
      border: 1px solid rgba(0,0,0, 0.1);
      border-radius: 50%;
      background: inherit;
      height: 48px;
      width: 48px;
      padding: 0;
      outline: none;
      cursor: pointer;
      &:active {
        position: relative;
        top: 1px;
      }
      img {
        position: relative;
        top: 1px;
      }
    }
  }
  .head, .clear {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .clear {
    margin-bottom: 10px;
    p {
      margin: 0;
    }
    button {
      font-weight: 300;
      font-size: 14px;
      opacity: 0.4;
      cursor: pointer;

      background: inherit;
      border: none;
      outline: none;
    }
  }
  .wrapper {
    height: calc(100% - 260px);
  }
  .container {
    height: 100%;
    overflow-y: auto;
    overscroll-behavior: contain;
  }
  .item {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid rgba(0,0,0, 0.1);
    padding: 12px 0;
    .helper {
      display: flex;
      align-items: center;
    }
    .preview {
      height: 96px;
      width: 96px;
      margin-right: 8px;
    }
    .about {
      width: 165px;
      p {
        font-weight: 300;
        font-size: 16px;
        letter-spacing: 0.02em;
      }
      span {
        font-weight: 600;
        font-size: 16px;
      }
    }
    .count {
      margin-right: 43px;
      display: flex;
      align-items: center;
      span {
        margin: 0 20px;
      }
      button {
        width: 40px;
        height: 24px;
        background: #F2F2F2;
        border-radius: 4px;
        border: none;
        outline: none;
        cursor: pointer;
      }
      .minus {
        background: url("../assets/img/icons/minus.svg") no-repeat 50% 50% #F2F2F2;
        background-size: 30%;
      }
      .plus {
        background: url("../assets/img/icons/plus.svg") no-repeat 50% 50% #F2F2F2;
        background-size: 40%;
      }
    }
  }
  .remove {
    border: none;
    outline: none;
    background: inherit;
    padding: 0;
    opacity: 0.3;
    cursor: pointer;
    &:active {
      position: relative;
      top: 1px;
    }
  }
  .message {
    margin-top: 40px;
  }
  .total {
    span {
      font-size: 16px;
    }
    p {
      font-weight: 500;
      font-size: 30px;
      letter-spacing: -0.02em;
    }
  }
  .footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 40px;
    .total {
      display: flex;
      flex-direction: column;
      p {
        margin: 0;
      }
    }
    .submit {
      background: #7BB899;
      border-radius: 4px;
      border: none;
      outline: none;

      font-weight: 500;
      font-size: 12px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      padding: 20px 60px;
      cursor: pointer;
      &:active {
        position: relative;
        top: 1px;
      }
    }
  }
  @media (max-width: 750px) {
    .cart {
      padding: 20px 24px;
      height: calc(100% - 40px);
      width: calc(100% - 48px);
    }
  }
  @media (max-width: 750px) {
    .cart {
      padding: 20px 24px;
      height: calc(100% - 40px);
